import React, { Fragment, useState } from "react";
import { useHistory } from "react-router-dom";
import Modal from "react-modal";
import ProgramData from "./modals/ProgramData";
import ProgramService from "../services/program-service";
import HorarioDesign from "./HorarioDesign";

const customStyles = {
  content: {
    width: "75%",
    height: "100%",
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    backgroundColor: "rgb(58, 58, 58)",
  },
};

Modal.setAppElement("#main");

const HorarioDesignController = (props) => {
  //let subtitle;
  const history = useHistory();
  const programService = new ProgramService();
  const data = props.data;

  const [value, setValue] = useState({
    data,
    programData: {
      _id: "",
      name: "",
      color: "#563d7c",
      announcer: [],
      moderator: "",
      day: 1,
      hourIni: "00",
      hourFin: "00",
      moderatorReport: "",
      announcerReport: ``
    },
  });

  const handleUpdate = async (e) => {
    const confirm = await programService.editProgram(value.programData);
    if (confirm.err) {
      console.log("Error al obtener la data");
      return;
    }
    console.log(confirm);
    closeModal();
    history.push("/");
    //e.preventDefault();
  };

  const handleDelete = async (e) => {
    const confirm = await programService.deleteProgram(value.programData);
    if (confirm.err) {
      console.log("Error al obtener la data");
      return;
    }
    console.log(confirm);
    closeModal();
    window.location.reload();
    //history.push("/");
    //e.preventDefault();
  };

  const [modalIsOpen, setIsOpen] = useState(false);

  const openModal = () => {
    setIsOpen(true);
  };

  const handleChangeIni = (e) => {
    setValue({
      programData: e,
    });
  };

  const handleChange = (e) => {
    setValue({
      programData: {
          ...value.programData,
          [e.target.name]: e.target.value
      }
    })
  }

  const handleSelectAnnouncer = e => {
    setValue(value => {
        const announcer = [...value.programData.announcer, e]
        
        return {
            programData: {
                ...value.programData,
                announcer
            }
        }
    })
}

const handlePopAnnouncer = (e) => {
  setValue((value) => {
      const index = value.programData.announcer.indexOf(e)

    const announcer = [
      ...value.programData.announcer.slice(0, index),
      ...value.programData.announcer.slice(index + 1),
    ];

    return {
      programData: {
        ...value.programData,
        announcer,
      },
    };
  });
};

  const afterOpenModal = () => {
    //subtitle.style.color = '#f00';
  };

  const closeModal = () => {
    setIsOpen(false);
  };
  return (
    <Fragment>
      <HorarioDesign
        data={data}
        handleChange={handleChangeIni}
        openModal={openModal}
      ></HorarioDesign>

      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <ProgramData
          form={value.programData}
          closeModal={closeModal}
          handleChange={handleChange}
          handleUpdate={handleUpdate}
          handleSelectAnnouncer={handleSelectAnnouncer}
          handlePopAnnouncer={handlePopAnnouncer}
          handleDelete={handleDelete}
        ></ProgramData>
      </Modal>
    </Fragment>
  );
};

export default HorarioDesignController;
