export default class PrettyService {
    
    days = {
        1: "Lunes",
        2: "Martes",
        3: "Miércoles",
        4: "Jueves",
        5: "Viernes",
        6: "Sábado",
        7: "Domingo"
    }

    hora = (a, b) => {
        let horario = "";
        if(a <= 12){
            if(a !== 12){
                horario += `${a}am a `;
            }else{
                horario += `${a}pm a `;
            }
        }else{
            if(a !== 24){
                horario += `${a - 12}pm a `
            }else{
                horario += `${a - 12}am a `
            }
        }
        if(b <= 12){
            if(b !== 12){
                horario += `${b}am`;
            }else{
                horario += `${b}pm`;
            }
        }else{
            if(b !== 24){
                horario += `${b - 12}pm`
            }else{
                horario += `${b - 12}am`
            }
        }
        return horario;
    }

    personas = (people) => {
        let ps = "";
        console.log("XDD")
        console.log(people)
        if(people[0]){
            for(let p of people){
                console.log(p)
                ps += `${p.name}, `
            }
            ps = ps.substring(0, ps.length - 2);
        }
        return ps;
    }


    
}
