import React, { Component } from "react";
import ProgramService from "../../services/program-service";
import "../../styles/formProgram.scss";

export default class FormPrograma extends Component {
  programService = new ProgramService();

  constructor(props) {
    super(props);

    this.state = {
      days: [
        "Lunes",
        "Martes",
        "Miércoles",
        "Jueves",
        "Viernes",
        "Sábado",
        "Domingo",
      ],
      mods: [],
      announcers: [],
      announcerSelected: "",
    };
  }

  render() {
    console.log(this.props.form);
    const { name, color, announcer, moderator, day, hourIni, hourFin } =
      this.props.form;

    const hoursIni = [];
    const hoursFin = [];

    for (let i = 0; i < 25; i++) {
      hoursIni.push(
        <option key={`hi${i}`} defaultValue={i + 1}>
          {i < 10 ? `0${i}` : i}
        </option>
      );
    }

    for (let i = parseInt(hourIni) + 1; i < 25; i++) {
      hoursFin.push(
        <option key={`hi${i}`} defaultValue={i + 1}>
          {i < 10 ? `0${i}` : i}
        </option>
      );
    }

    return (
      <form onSubmit={this.props.handleSubmit}>
        <section className="form--program__container">
          <div className="form--title">
            <h1>Nuevo Programa</h1>
          </div>
          <div className="form--body">
            <div className="mb-3">
              <label htmlFor="name" className="form-label">
                Nombre del Programa
              </label>
              <input
                onChange={this.props.handleChange}
                type="text"
                className="form-control"
                id="name"
                name="name"
                value={name}
              />
            </div>
            <div className="mb-3">
              <label htmlFor="announcerSelected" className="form-label">
                Locutor@
              </label>
              <div className="d-flex w-100">
                <select
                  onChange={(e) => {
                    this.handleChange(e);
                  }}
                  id="announcerSelected"
                  className="form-select"
                  name="announcerSelected"
                  value={this.state.announcerSelected}
                >
                  <option></option>
                  {this.state.announcers.map((e, i) => (
                    <option key={`a${i}`} value={e._id}>
                      {e.name}
                    </option>
                  ))}
                </select>
                <button
                  onClick={(e) => {
                    this.props.handleSelectAnnouncer(
                      this.state.announcerSelected
                    );
                  }}
                  type="button"
                  className="btn btn-primary ms-3"
                >
                  +
                </button>
              </div>
              {announcer.map((e, i) => {
                return (
                  <div
                    className="alert alert-dark announcers d-flex justify-content-between mt-3"
                    key={`an${i}`}
                    value={e._id}
                  >
                    <p className="p-0 m-0">{e}</p>
                    <button
                      onClick={(d) => {
                        this.props.handlePopAnnouncer(e);
                      }}
                      type="button"
                      className="btn btn-danger"
                    >
                      X
                    </button>
                  </div>
                );
              })}
            </div>
            <div className="mb-3">
              <label htmlFor="moderator" className="form-label">
                Moderador@
              </label>
              <select
                onChange={this.props.handleChange}
                id="moderator"
                className="form-select"
                name="moderator"
                value={moderator}
              >
                <option></option>
                {this.state.mods.map((e, i) => (
                  <option key={`m${i}`} value={e._id}>
                    {e.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="mb-3">
              <label htmlFor="color" className="form-label">
                Color
              </label>
              <input
                onChange={this.props.handleChange}
                type="color"
                className="form-control form-control-color"
                id="color"
                name="color"
                value={color}
              />
            </div>
            <div className="mb-3">
              <label htmlFor="day" className="form-label">
                Día
              </label>
              <select
                onChange={this.props.handleChange}
                id="day"
                className="form-select"
                name="day"
                value={day}
              >
                {this.state.days.map((e, i) => (
                  <option key={`d${i}`} value={i + 1}>
                    {e}
                  </option>
                ))}
              </select>
            </div>
            <div className="d-flex justify-content-around w-100">
              <div className="mb-3">
                <label htmlFor="hourIni" className="form-label">
                  Hora Inicio
                </label>
                <div className="d-flex align-items-center">
                  <select
                    onChange={this.props.handleChange}
                    id="hourIni"
                    className="form-select"
                    style={{ width: "4.5rem" }}
                    name="hourIni"
                    value={hourIni}
                  >
                    {hoursIni}
                  </select>
                  <div className="mx-2">: 00</div>
                </div>
              </div>
              <div className="mb-3">
                <label htmlFor="hourFin" className="form-label">
                  Hora Fin
                </label>
                <div className="d-flex align-items-center">
                  <select
                    onChange={this.props.handleChange}
                    id="hourFin"
                    className="form-select"
                    style={{ width: "4.5rem" }}
                    name="hourFin"
                    value={hourFin}
                  >
                    {hoursFin}
                  </select>
                  <div className="mx-2">: 00</div>
                </div>
              </div>
            </div>
          </div>
          <div className="form--footer">
            <button
              onClick={this.props.closeModal}
              type="button"
              className="btn btn-danger"
            >
              Cerrar
            </button>
            <button type="submit" className="btn btn-info">
              Enviar
            </button>
          </div>
        </section>
      </form>
    );
  }

  componentDidMount() {
    this.getAnnouncers();
    this.getMods();
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  async getMods() {
    const data = await this.programService.listPersonByRole({
      role: "moderador",
    });
    if (data.err) {
      console.log(data.err);
      return;
    }
    this.setState({
      mods: data,
    });
  }

  async getAnnouncers() {
    const data = await this.programService.listPersonByRole({
      role: "locutor",
    });
    if (data.err) {
      console.log(data.err);
      return;
    }
    this.setState({
      announcers: data,
    });
  }

  async nameAnnouncer(id) {
    const data = await this.programService.getPersonById({ id });
    return data.name;
  }
}
