import React, { Component, Fragment } from 'react'
import Footer from './Footer';
import NavBar from './NavBar'

export default class Layout extends Component {
    constructor(props){
        super(props);

        this.state = {
            user: "2529",
        };
    }

    render() {
        return (
            <Fragment>
                <NavBar user={this.state.user}></NavBar>
                <main>
                    {this.props.children}
                </main>
                <Footer></Footer>
            </Fragment>
        )
    }
}
