import React, { Component, Fragment } from 'react'
import HorarioDesignController from '../components/HorarioDesignController'
import HorarioController from '../components/HorarioController'
import ProgramService from '../services/program-service'

import '../styles/horario.scss'

export default class Horario extends Component {
    programService = new ProgramService()

    constructor(props){
        super(props);

        this.state = {
            data: []
        }
    }

    componentDidMount(){
        this.listPrograms()
    }

    render() {
        return (
            <Fragment>
                <div className="schedule">
                    <HorarioDesignController data={this.state.data}></HorarioDesignController>
                </div>
                <div className="btns-container">
                    <HorarioController></HorarioController>
                </div>
            </Fragment>
        )
    }

    async listPrograms(){
        const data = await this.programService.listPrograms()
        if(data.err){
            console.log("Error al obtener la data")
            return
        }
        this.setState({
            data
        })
    }
}
