import React, { Component, Fragment } from 'react';
import Modal from 'react-modal';
import '../styles/horarioDesign.scss';

Modal.setAppElement('#main');

export default class HorarioDesign extends Component {
    constructor(props){
        super(props)
        this.state = {
            days: ["", "Lunes", "Martes", "Miércoles", "Jueves", "Viernes", "Sábado", "Domingo"],
            data: []
        };
    }
    
    componentDidMount(){
        
    }

    componentDidUpdate(){
        console.log(`updated!`)
    }

    render() {
        const items = [];

        for (let i = 0; i < 24; i++) {
          items.push(<div key={i} className="box-hour">
                        {i < 10 ? `0${i}` : i}:00 - 
                        {i < 10 ? `0${i}` : i}:59
                    </div>)
        }

        return (
            <Fragment>
                <div className="schedule-container">
                    <div className="schedule_days-container">
                        {
                            this.state.days.map((e, i) => 
                                <div key={i} className="box-day">{e}</div>
                            )
                        }
                    </div>
                    <div className="schedule_hours-continer">
                        {items}
                    </div>
                    <div id="programs" className="schedule_program-container">
                        {
                            this.props.data.map((e, i) => 
                                <div
                                    id={i}
                                    key={`data${i}`}
                                    type="button"
                                    onClick={() => {
                                        this.props.handleChange(e)
                                        this.props.openModal()
                                    }}
                                    className="box-program"
                                    style={{
                                        gridColumn: e.day,
                                        gridRow: `${e.hourIni + 1} / ${e.hourFin + 1}`,
                                        backgroundColor: e.color
                                    }}
                                >
                                    <p>{e.name}</p>
                                </div>
                            )
                        }
                    </div>
                </div>
            </Fragment>
        )
    }

    detailsProgram = e => {
        console.log(this.props.data[e.target.id])
    }
}
