import React, { Component } from "react";

export default class Reports extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    let { report } = this.props

    return (
      <div>
        <textarea
          className="form-control"
          value={report}
          rows="25"
          readOnly
        ></textarea>
      </div>
    );
  }
}
