//const { URLSearchParams } = require('url');
const URL = "";

export default class ProgramService {

    async listPrograms(){
        return await fetch(`${URL}/api/program`, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
        .then(res => res.json())
        .then(data => data)
        .catch(err => {
            return []
        })
    }
    
    async addProgram(body){
        return await fetch(`${URL}/api/program`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
        .then(res => res.json())
        .then(data => data)
        .catch(err => {
            return {
                err
            }
        })
    }

    async listPersonByRole(body){
        return await fetch(`${URL}/api/person/role?`+ new URLSearchParams(body), 
        {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            //body: JSON.stringify(body)
        })
        .then(res => res.json())
        .then(data => data)
        .catch(err => {
            return {
                err
            }
        })
    }

    async getPersonById(body){
        return await fetch(`${URL}/api/person/id?` + new URLSearchParams(body), 
        {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            //body: JSON.stringify(body)
        })
        .then(res => res.json())
        .then(data => data)
        .catch(err => {
            return {
                err
            }
        })
    }

    async editProgram(body){
        return await fetch(`${URL}/api/program`, 
        {
            method: 'PUT',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
        .then(res => res.json())
        .then(data => data)
        .catch(err => {
            return {
                err
            }
        })
    }

    async deleteProgram(body){
        return await fetch(`${URL}/api/program`, 
        {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        })
        .then(res => res.json())
        .then(data => data)
        .catch(err => {
            return {
                err
            }
        })
    }

    async readReports(){
        return await fetch(`${URL}/api/program/reports`, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
        .then(res => res.json())
        .then(data => data)
        .catch(err => {
            return []
        })
    }
}
