        return (
            <Fragment>
                <footer>
                    <div className="first--content content">
                        Radio Go! <br />
                        Conectados contigo
                        </div>
                    <div className="second--content content">
                        <img src="https://res.cloudinary.com/xizascloud/image/upload/v1630184411/rgo/rgo_iksbua.png" alt="rgo_logo" />
                    </div>
                    <div className="third--content content">
                       <a href="https://radiogolatino.blogspot.com/">
                           radiogolatino.blogspot.com
                        </a>
                    </div>
                </footer>
            </Fragment>  
        )
